import React, {  useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import '../App.css'; // Import your CSS for styling

const Home = () => {
  const navigate = useNavigate();

  useEffect(() => {
    // Fetch the previously saved papers from localStorage
  
  }, []);

  const goToCreateQP = () => {
    navigate('/create-question-paper');
  };


  return (
    <>
    <div className="home-container">
        <div className='bg_1'>
            
      <header className="home-header">
        <h1>Welcome to the Paper Creator</h1>
        <p>Create, attend, and download your question papers seamlessly.</p>
      </header>

      <div className="home-content">
        <button className="create-btn" onClick={goToCreateQP}>Create QP</button>
      </div>

    

      <footer className="home-footer">
        <p>Enhance your testing experience with our tools!</p>
      </footer>
      
        </div>
    </div>
    </>
  );
};

export default Home;
