import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import '../App.css';  // Import CSS
import jsPDF from 'jspdf';
import 'jspdf-customfonts';  // Import custom fonts
import hindiFont from '../assets/fonts/Devnew.ttf'; // Path to your custom Hindi font


const AttendPaper = () => {
  const [questions, setQuestions] = useState([]);
  const [answers, setAnswers] = useState({});
  const [schoolName, setSchoolName] = useState('');
  const navigate = useNavigate();

  useEffect(() => {
    const storedQuestions = JSON.parse(localStorage.getItem('questions'));
    const storedAnswers = JSON.parse(localStorage.getItem('answers'));
    const storedSchoolName = localStorage.getItem('schoolName'); // Retrieve school name
    if (storedQuestions) {
      setQuestions(storedQuestions);
    }
    if (storedAnswers) {
      setAnswers(storedAnswers);
    }
    if (storedSchoolName) {
      setSchoolName(storedSchoolName);
    }
  }, []);

  const handleAnswerChange = (index, value) => {
    setAnswers({ ...answers, [index]: value });
  };

  const submitAnswers = () => {
    localStorage.setItem('answers', JSON.stringify(answers));
    navigate('/result');
  };

  

  const generatePDF = () => {
    const doc = new jsPDF();

    // Load the custom font
    doc.addFileToVFS('HindiFont.ttf', hindiFont);
    doc.addFont('HindiFont.ttf', 'HindiFont', 'normal');
    doc.setFont('HindiFont');

    // Set font size, make text bold and underlined, center-align school name
    doc.setFontSize(26);
    doc.setFont('HindiFont', 'bold');
    doc.text(schoolName, doc.internal.pageSize.getWidth() / 2, 20, { align: 'center' });
    doc.setLineWidth(0.5);
    doc.line(20, 25, 190, 25); // Underline

    // Reset font for the rest of the document
    doc.setFontSize(12);
    doc.setFont('HindiFont', 'normal');

    let currentY = 40; // Start after the school name

    // Check if there are questions before generating the PDF
    if (questions.length > 0) {
      questions.forEach((question, index) => {
        const questionText = `${index + 1}. ${question.questionText}`;
        doc.text(questionText, 10, currentY);
        currentY += 10; // Move Y position down for next content

        if (question.answerType === 'MCQ') {
          question.choices.forEach((choice, i) => {
            doc.text(`${String.fromCharCode(65 + i)}. ${choice}`, 20, currentY);
            currentY += 8; // Move Y position down for next choice
          });
        } else {
          currentY += 10; // Add extra space for theory questions to prevent overlap
        }

        currentY += 5; // Add some space after each question
      });
    } else {
      doc.text("No questions available.", 10, currentY); // Handle case where no questions are added
    }

    // Add date and time to the filename
    const now = new Date();
    const formatDate = (date) => {
      const day = String(date.getDate()).padStart(2, '0');
      const month = String(date.getMonth() + 1).padStart(2, '0'); // Months are zero-based
      const year = date.getFullYear();
      const hours = String(date.getHours()).padStart(2, '0');
      const minutes = String(date.getMinutes()).padStart(2, '0');
      const seconds = String(date.getSeconds()).padStart(2, '0');
      return `${day}-${month}-${year}at${hours}${minutes}${seconds}`;
    };
    const dateStr = formatDate(now);
    const fileName = `Solution-${dateStr}.pdf`;

    // Save the PDF
    doc.save(fileName);
};




  return (

    <>
    <div className="navBar">
    
    <h1 className='nab-h1'>Attend Question Paper</h1>
      
      </div>
    <div className="container">
    <h1>{schoolName}</h1>
     
      {questions.map((question, index) => (
        <div className="question-box-attend" key={index}>
          <p>{index + 1}. {question.questionText}</p>
          {question.answerType === 'MCQ' ? (
            question.choices.map((choice, i) => (
              <label key={i}>
                <input
                  type="radio"
                  name={`question-${index}`}
                  value={choice}
                  onChange={(e) => handleAnswerChange(index, e.target.value)}
                />
                {choice}
              </label>
            ))
          ) : (
            <textarea
              placeholder="Type your answer here..."
              onChange={(e) => handleAnswerChange(index, e.target.value)}
            />
          )}
        </div>
      ))}
      
     <div style={{ justifyContent: 'space-between', display: 'flex',flexDirection: "column" }}>
     <button className="pdf-download" onClick={generatePDF}>Download Paper as PDF</button>
     <button className='secondary' onClick={submitAnswers}>Submit Answers</button>
     <button className="new-paper" onClick={() => window.location.href = '/'}>Create New Paper</button>
     </div>
    </div>
    </>
  );
};

export default AttendPaper;
