import React from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import CreateQuestionPaper from './components/CreateQuestionPaper';
import AttendPaper from './components/AttendPaper';
import Result from './components/Result';
import Home from './components/Home';

function App() {
  return (
    <Router>
      <Routes>
      <Route path="/" element={<Home/>} />
      <Route path="/Home" element={<Home/>} />
        <Route path="/create-question-paper" element={<CreateQuestionPaper/>} />
        <Route path="/attend" element={<AttendPaper />} />
        <Route path="/result" element={<Result />} />
      </Routes>
    </Router>
  );
}

export default App;
